import axios from "../../axios";

const state = {
    user: {}
};

const getters = {
    user: state => state.user
};

const actions = {
    async userRegistration({ commit }, data) {
        const response = await axios.post("/api/auth/register", data);
        commit("setUser", response.data.user);
    }
};

const mutations = {
    setUser: (state, user) => {
        state.user = user;
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};
